<template>
    <div class="workers-component">
        <h1 class="main-header">Pokoje</h1>
        <div class="cards-row">
            <div class="card-3">
                <h1 class="card-title">Tworzenie pokoju</h1>
                <div class="card-content">
                    <form class="add-room-form" @submit.prevent="addRoom">
                        <div class="form-cols">
                            <div class="form-col">
                                <md-field>
                                    <label>Numer pokoju</label>
                                    <md-input required type="number" v-model="room.number"></md-input>
                                </md-field>
                                <md-field>
                                    <label for="type">Rodzaj pokoju</label>
                                    <md-select required v-model="room.type" name="type" id="type">
                                        <md-option value="single">Pokój jednoosobowy</md-option>
                                        <md-option value="double">Pokój dwuosobowy</md-option>
                                        <md-option value="apartment">Apartament</md-option>
                                    </md-select>
                                </md-field>
                                <md-field>
                                    <label for="type">Standard pokoju</label>
                                    <md-select required v-model="room.standard" name="type" id="type">
                                        <md-option value="standard">Standard</md-option>
                                        <md-option value="premium">Premium</md-option>
                                    </md-select>
                                </md-field>
                            </div>
                            <div class="form-col">
                                <md-field>
                                    <label>Maksymalna liczba gości</label>
                                    <md-input required type="number" v-model="room.maxGuests"></md-input>
                                </md-field>
                                <md-field>
                                    <label for="role">Dostępny do wynajmu</label>
                                    <md-select required v-model="room.available" name="role" id="role">
                                        <md-option value="true">Tak</md-option>
                                        <md-option value="false">Nie</md-option>
                                    </md-select>
                                </md-field>
                            </div>
                        </div>
                        <button type="submit">Dodaj pokój</button>
                    </form>
                </div>
            </div>
            <div class="card-1">
                <h1 class="card-title">Ilość pokoi</h1>
                <div class="card-content">
                    <div class="department-box gold">
                        <p>Pokoje jednoosobowe</p>
                        <div class="amount">
                            <span>{{singleRooms}}</span>
                            <img src="@/assets/img/worker.png" alt="person icon">
                        </div>
                    </div>
                    <div class="department-box blue ">
                        <p>Pokoje dwuosobowe</p>
                        <div class="amount">
                            <span>{{doubleRooms}}</span>
                            <img src="@/assets/img/worker.png" alt="person icon">
                        </div>
                    </div>
                    <div class="department-box dark">
                        <p>Apartamenty</p>
                        <div class="amount">
                            <span>{{apartments}}</span>
                            <img src="@/assets/img/worker.png" alt="person icon">
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="cards-row">
            <div class="card-5">
                <h1 class="card-title">Lista pokoi</h1>
                <div class="card-content">
                    <div class="rooms-list">
                        <div class="list-header">
                            <p class="room-type list-col-10">Numer</p>
                            <p class="room-type list-col-20">Typ</p>
                            <p class="room-standard list-col-15">Standard</p>
                            <p class="room-maxGuests list-col-20">Maksymalna liczba gosci</p>
                            <p class="room-available list-col-20">Dostępność</p>
                        </div>
                        <div v-for="room in rooms" class="room-row" :key="room._id">
                            <p class="room-type list-col-10">{{room.number}}</p>
                            <p class="room-type list-col-20">{{room.type | typeFilter}}</p>
                            <p class="room-standard list-col-15">{{room.standard | standardFilter}}</p>
                            <p class="room-maxGuests list-col-20">{{room.maxGuests}}</p>
                            <p class="room-available list-col-20">{{room.available | availableFilter}}</p>
                            <div class="room-delete list-col-15">
                                <button @click="deleteRoom(room._id)">Usuń</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import axios from 'axios';
    axios.defaults.baseURL = 'https://hotel-back.cleverapps.io';
    // axios.defaults.baseURL = 'http://localhost:3000';
    export default {
        data() {
            return {
                room: {
                    number: 0,
                    type: '',
                    standard: '',
                    maxGuests: 0,
                    available: true
                },
                singleRooms: 0,
                doubleRooms: 0,
                apartments: 0,
                rooms: []
            }
        },
        beforeMount() {
            this.getRooms();
        },
        methods: {
            getRooms() {
                axios.get('/get-rooms')
                    .then(res => {
                        /* eslint-disable */
                        console.log(res.data.rooms);
                        const rooms = res.data.rooms;
                        this.rooms = rooms;
                        rooms.forEach(room => {
                            if (room.type == 'single') {
                                this.singleRooms += 1;
                            } else if (room.type == 'double') {
                                this.doubleRooms += 1;
                            } else if (room.type == 'apartment') {
                                this.apartments += 1;
                            }
                        })
                    })
            },
            deleteRoom(id) {
                axios.post('/delete-room', {
                        id: id
                    })
                    .then(res => {
                        /* eslint-disable */
                        console.log(res.data);
                        this.$router.go();
                    })
            },
            addRoom() {
                axios.post('/add-room', {
                        room: this.room
                    })
                    /* eslint-disable */
                    .then(res => {
                        this.$router.go();
                    })
            }
        },
        filters: {
            typeFilter(type) {
                if (type == 'single') {
                    return "Pokój jednoosobowy";
                } else if (type == 'double') {
                    return "Pokój dwuosobowy";
                } else if (type == 'apartment') {
                    return "Apartament";
                }
            },
            standardFilter(standard) {
                if (standard == 'standard') {
                    return "Standard";
                } else if (standard == 'premium') {
                    return "Premium";
                }
            },
            availableFilter(available) {
                if (available == true) {
                    return "Dostępny";
                } else if (available == false) {
                    return "Niedostępny";
                }
            }
        },
        watch: {
            'room.type': function (newType, oldType) {
                console.log(type);
                if (newType == 'single') {
                    this.room.maxGuests = 1;
                } else if (newType == 'double') {
                    this.room.maxGuests = 2;
                } else if (newType == 'apartment') {
                    this.room.maxGuests = 4;
                }
            }
        }
    }
</script>